header {
  width: 100%;
  height: 7rem;

  display: flex;
  justify-content: center;
  align-items: center;
}

.Header {
  width: 80%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.Header ul {
  list-style: none;
  width: 500px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.Header ul li {
  cursor: pointer;
  display: inline-flex;
}

.Header ul li a {
  font-style: normal;
  font-weight: normal;
  text-decoration: none;
  color: inherit;
}
