.FreeAd__Wrapper {
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 56vh;
  background: #e6ebf2;
}

.FreeAd__Main {
  margin: auto;
  width: 80%;
  height: 100%;
  display: flex;
  justify-content: space-between;
}

.FreeAd__Main aside {
  width: 50%;
  height: 100%;
}

.FreeAd__Main__Text {
  padding-top: 12%;
}

.FreeAd__Main__Text p {
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 28px;

  color: #000000;
}

.FreeAd__Main__Text span {
  display: inline-block;
  padding-top: 7%;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 150%;
  /* or 21px */

  color: rgba(0, 0, 0, 0.75);
}

.FreeAd__Footer {
  margin-top: 20%;
  margin-left: -7%;
}

.FreeAd__Footer__Wrapper {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.FreeAd__Footer__text p {
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 28px;

  color: #000000;
}

.FreeAd__Footer__text span {
  display: inline-block;
  padding-top: 0.9rem;

  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 150%;
  /* identical to box height, or 21px */

  color: rgba(0, 0, 0, 0.75);
}

.FreeAd__Footer__links ul {
  list-style: none;

  margin-top: 2.9rem;
  display: flex;
}

.FreeAd__Footer__links ul li {
  margin-left: 6%;
}
