.Main {
  /* Area */

  width: 100%;
  height: 100vh;

  position: absolute;
  left: 0%;
  right: 0%;
  top: 0%;
  bottom: 0%;

  background: #112963;

  z-index: -1000;

  overflow: hidden;
}

.Main__Text {
  padding: 8% 10%;
  display: flex;
  flex-direction: column;
  align-items: left;
}

.Main__Text p {
  color: white;
  font-style: normal;
  font-weight: bold;
  font-size: 36px;
  line-height: 150%;
  /* or 54px */

  letter-spacing: 0.05em;
}

.Main__Text span {
  display: inline-block;
  padding-top: 3%;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 150%;
  /* or 27px */

  color: rgba(255, 255, 255, 0.8);
}

.Main__Text button {
  margin-top: 5%;
  width: 159px;
  height: 48px;
  border-radius: 100px;
  background: transparent;
  border: 1px solid #ffffff;
  color: white;
}

.CircleOne {
  z-index: -1;
  position: absolute;
  right: 54.3%;
  top: 16.15%;
  bottom: -10.94%;
}

